.language {
    cursor: pointer;
    margin: 5px 0;
    padding: 5px;

    &--check {
        display: inline-block;

        &--visible {
            opacity: 1;
        }
        &--hidden {
            opacity: 0;
        }

        color: mediumseagreen;
        float: right;
        margin-left: 4px;
    }

    &--icon {
        font-size: 1.25em;

        &:hover {
            color: @menu-hover;
        }
    }

    &--list {
        text-decoration-style: unset;
        position: absolute;
        right: 0;
        border: 1px solid;
        background-color: @menu-color;
        font-size: .9em;

        &--hidden {
            display: none;
        }

        &--visible {
            display: inline-block;
        }

        &--option {
            padding: 5px;
            margin: 0;
            border-bottom: 1px solid black;

            &:hover {
                background-color: @menu-hover;
            }

            &::selection {
                color: none;
                background: none;
            }

            &:last-child {
                border-bottom: none;
            }
        }
    }
}
